@import url(https://fonts.googleapis.com/css2?family=Rochester&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{
    background-color:#000000;   
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";   
  }
  

body {
  font-family: "Frutiger";
  overflow-x: hidden;
}

.top-wrapper {
  padding: 100px 0 100px 0;
  color: white;
  text-align: center;
  height:100vh;
  position: relative;
  z-index: 1;
}
.video-container {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  object-fit: cover;
  z-index: -1;
}
p{
  color:white;
}


.nav-spacing
{
  padding-left: 5rem !important;
}

.main {
  height:auto;
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}
.about-us{
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
#about-img{
  transition: all .2s ease-in-out;
  width: 450px;
  height: 450px;
}
#about-img:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor:pointer;
}
#values-img{
  transition: all .2s ease-in-out;
  width: 450px;
  height: 450px;
}
#values-img:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor:pointer;
}
#vision-img{
  transition: all .2s ease-in-out;
  width: 450px;
  height: 450px;
}
#vision-img:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor:pointer;
}

@media ( max-width : 700px){
  .main { flex-wrap:wrap;}
}
.content{
  display : flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  overflow: hidden;
}



.events
{
    padding:20px 20px;
    height:auto;
    width: auto;
}
.events-header
{
        text-align: center;
        color:white;
        font-family: cursive;
        text-transform: uppercase;
         font-weight: 50px;
         letter-spacing: 8px;
         font-size: 60px;
         margin-bottom: 80px;
         margin-top: 30px;
         padding-top: 100px;
}

.card-style{
    display: flex;
    flex-direction: flex-start;
    flex-wrap: wrap;
    justify-content: center;
}

.card{
    max-width: 500px;
    max-height: auto;
    background-color:#CCC;
    margin: 30px;
}
.card-text{
    font-size: 15px;
}
.card:hover{
    cursor: pointer;
    background-color: #0f1a24;
    transition: all 0.1s cubic-bezier(0.5, 0.35, 0.15, 0); 
}

.card:hover{
    padding:10px 10px;
    color:white;
}
.col-md-8:hover{
    background-color:#000000;
}

.footer
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #030607;
    max-height: auto;
    color:white !important;
}
.footer-header
{
    color:white !important;
    height:50px;
    background-color:#131414;
    font-weight: bolder;
    font-size: 50px;
    cursor: default;
    display:flex;
    align-items: center;
    justify-content: center;
}
.footer-body
{
    height:auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap:  wrap;
}


.contact
{
    height:auto;
    font-weight: bolder;
    padding:0px 20px 0px 20px;
    display: flex;
}

.contact ul
{
    list-style-type: none;
}

.contact li
{
    cursor: pointer;
    padding-top:10px;
    padding-bottom: 10px;
}

.footer-end
{
    background-color: #131414;
    
}
.footer-link{
    color:white !important;
}
.yt-icon:hover{
    color:#FF0000;
}
.git-icon:hover{
    color:#333;
}
.twitter-icon:hover{
    color:#00acee;
}
.li-icon:hover{
    color:#0e76a8;
}
.ig-icon:hover{
    color:#8a3ab9;
}

.social-media
{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height:70px;
}

.social-media span:hover
{
    cursor: pointer;
}

.feedback{

    padding: 30px 50px 10px 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

@media( max-width: 900px){
    .footer-body{display:flex;
    align-items: center;
    flex-direction: column;
    padding:0px ;
    }
}
@media  screen 
    and (max-width: 400px) {
    input {width:300px; }
    textarea {width:300px; }
}
.feedback-box{
    padding : 0px 20px 0px 20px;
    font-size: 25px;
