    color: rgb(32, 32, 28);
}
.modal-bg .modal-content{
    background-color: #161616;
    color: #FFFFFF;
}


.sop
{
    display: grid;
   grid-template-columns: repeat(auto-fit,minmax(345px, 0.25fr)) ;
   justify-content: center;
