}
  
button {
    background-color: transparent;
    float: right;
    border: none;
}
#open {
    color: rgb(20,193,203);
    position: fixed;
    right: 0;
    z-index: 1000;
    /*animation-name: shake;
    animation-duration: 3s;
    animation-direction: alternate;
    animation-iteration-count: infinite;*/
}
.menuIcon{
    font-size: 2rem !important;
    -webkit-animation: shake 2s ease-in-out infinite;
            animation: shake 2s ease-in-out infinite;

}

@-webkit-keyframes shake{
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%{
        -webkit-transform: scale(1.6);
                transform: scale(1.6);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);;
    }
}

@keyframes shake{
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%{
        -webkit-transform: scale(1.6);
                transform: scale(1.6);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);;
    }
}
#close{
    color:#151320;
}
#open:hover {
    color: white;
}
#close:hover {
    color: red;
}
.sidebar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
  }
.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:#808080;
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    box-shadow: black;
    transition:  all 0.3s linear;;
    -webkit-transform: translate(-100%);;
            transform: translate(-100%);
    z-index:10;
  }
  .show-sidebar {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
  .sidebar-toggle {
    -webkit-transform: translate(0);
            transform: translate(0);
}
.social-icons {
    align-items: center;
    display: flex;
    padding-bottom: 2rem;
    font-size: 1.5rem;
    margin: 0;
    justify-content: space-around;
    padding-left: 0.1rem ;
  }
  .icon4:hover{
    color:#FF0000;
}
.icon1:hover{
    color:#333;
}
.icon3:hover{
    color:#00acee;
}
.icon5:hover{
    color:#0e76a8;
}
.icon2:hover{
    color:#8a3ab9;
}
@media screen and (min-width: 676px) {
    .sidebar {
      width: 400px;
    }
  }
